.reviews {
  margin: -175px 0 96px;
  padding-top: 175px; }
  .reviews__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .reviews__item {
    padding: 17px 24px;
    border: 1px solid #ebebeb;
    cursor: pointer;
    overflow: hidden; }
    .reviews__item-author-name {
      color: #272c35;
      font-size: 18px;
      line-height: 27px;
      font-weight: 700; }
    .reviews__item-date {
      margin-top: 4px;
      color: #5b626f;
      font-size: 12px;
      line-height: 18px; }
    .reviews__item-comment {
      color: #5b626f;
      font-size: 15px;
      line-height: 130%; }
  .reviews__star-rating {
    margin: 8px 0; }
  .reviews__write-review-btn {
    margin-top: 32px;
    padding: 22px 48px; }
