.product {
  padding: 64px 0 94px; }
  .product__left {
    height: 100%; }
  .product__wrapper {
    display: flex; }
  .product__media-content {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 6px;
    max-width: 820px;
    width: 100%; }
    .product__media-content img,
    .product__media-content iframe {
      width: 100%;
      min-height: 300px;
      height: auto; }
  .product__info {
    position: sticky;
    top: 139px;
    max-width: 400px;
    width: 100%;
    height: fit-content;
    margin-left: 40px; }
    .product__info-basic {
      margin-top: 42px;
      padding-bottom: 24px;
      border-bottom: 1px solid #ebebeb; }
  .product .advances-block {
    margin-top: 90px; }
    .product .advances-block__items,
    .product .advances-block .advances__items {
      display: grid;
      grid-template-columns: repeat(5, minmax(0, 1fr));
      margin-top: 30px; }
  .product .advances-block-swiper .advances__items {
    margin-top: 30px; }
  .product__media-swiper {
    display: none; }
  .product__details-basic {
    margin-top: 8px;
    font-size: 12px;
    line-height: 18px; }
  .product__id {
    color: #5b626f; }
  .product__availability {
    margin-left: 8px;
    color: #39c46e; }
    .product__availability--is-delivered {
      color: #ffc800; }
    .product__availability--missing {
      color: #eb5757; }
  .product__price {
    margin: 8px 0;
    color: #000;
    font-size: 24px;
    font-weight: 700;
    line-height: 36px; }
    .product__price span {
      margin-right: 4px; }
  .product__text {
    color: #5b626f;
    font-size: 15px;
    line-height: 130%; }
  .product__form {
    margin-top: 24px; }
  .product__checkbox-colors {
    margin-bottom: 14px; }
    .product__checkbox-colors .product__checkbox-item input {
      width: 36px;
      height: 36px;
      margin: 0 10px 10px 0;
      border: 2px solid #d9d9d9;
      border-radius: 100%; }
      .product__checkbox-colors .product__checkbox-item input:checked {
        border: 2px solid #272c35; }
  .product__checkbox-sizes {
    margin-bottom: 20px; }
    .product__checkbox-sizes .product__checkbox-item div {
      position: relative;
      margin: 0 4px 4px 0; }
      .product__checkbox-sizes .product__checkbox-item div label {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: #000;
        font-size: 18px;
        line-height: 27px;
        font-weight: 700; }
      .product__checkbox-sizes .product__checkbox-item div input {
        position: relative;
        width: 40px;
        height: 40px;
        border: 1px solid #ebebeb;
        border-radius: 0; }
        .product__checkbox-sizes .product__checkbox-item div input:checked {
          border: 1px solid #000; }
  .product__checkbox-item {
    display: flex;
    flex-wrap: wrap; }
    .product__checkbox-item input {
      transition: 0.2s all linear;
      cursor: pointer;
      outline: none;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none; }
  .product__checkbox-label {
    font-size: 15px;
    line-height: 130%; }
  .product__checkbox-item {
    margin-top: 16px; }
  .product button {
    width: 100%; }
  .product__buy-btn {
    padding: 22px 48px;
    line-height: 27px; }
  .product__add-to-cart-btn {
    padding: 21px 48px;
    margin-top: 16px; }
  .product__preferences {
    display: flex;
    justify-content: space-between;
    margin-top: 24px; }
  .product__testimonials {
    display: flex;
    align-items: center; }
  .product__reviews {
    color: #5b626f;
    font-size: 12px;
    line-height: 16px; }
    .product__reviews:hover {
      text-decoration: underline; }
  .product__star-rating {
    margin-left: 10px; }
  .product__details {
    margin-top: 28px; }
    .product__details-description {
      margin-bottom: 16px; }
      .product__details-description .product__details-title {
        color: #000; }
      .product__details-description-items {
        padding-left: 25px; }
        .product__details-description-items li {
          color: #5b626f;
          font-size: 15px;
          line-height: 19.5px; }
    .product__details-title {
      color: #272c35;
      font-size: 18px;
      font-weight: 700;
      line-height: 27px; }
    .product__details-link {
      display: flex;
      justify-content: space-between;
      padding: 16px 0; }
      .product__details-link img {
        transform: rotate(180deg); }
    .product__details-link + .product__details-link {
      border-top: 1px solid #ebebeb; }
    .product__details-specifications {
      position: relative;
      flex-direction: column; }
      .product__details-specifications-head {
        display: flex;
        width: 100%;
        justify-content: space-between; }
      .product__details-specifications-body {
        visibility: hidden;
        height: 0;
        font-size: 0;
        margin: 10px 0 0 25px;
        font-weight: 400;
        transition: all 0.2s; }
        .product__details-specifications-body.active {
          visibility: visible;
          height: fit-content;
          font-size: 16px; }
        .product__details-specifications-body p {
          font-size: 14px;
          font-weight: 700; }
          .product__details-specifications-body p span + span {
            font-weight: 400; }
  @media (max-width: 1200px) {
    .product__wrapper {
      justify-content: center; }
    .product__media-content {
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: 400px;
      overflow: hidden; } }
  @media (max-width: 900px) {
    .product__wrapper {
      justify-content: center; }
    .product__media-content {
      display: none; }
    .product__info {
      max-width: 100%;
      margin-left: 0; }
    .product__media-swiper {
      display: block;
      margin: 30px 0; }
      .product__media-swiper img,
      .product__media-swiper iframe {
        max-width: 400px;
        min-height: none;
        max-height: 400px;
        width: 100%;
        height: 100%; }
      .product__media-swiper .swiper-wrapper .swiper-slide {
        display: flex;
        justify-content: center;
        overflow: hidden; }
    .product .advances-block {
      display: none; } }
  @media (max-width: 400px) {
    .product__add-to-cart-btn {
      padding: 21px 45px; } }
  @media (min-width: 901px) {
    .product .advances-block-swiper {
      display: none; } }
