@charset "UTF-8";
.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 100px;
  color: #272c35; }
  .container button {
    transition: all 0.2s; }
  .container a:hover {
    color: inherit; }
  @media (max-width: 750px) {
    .container {
      padding: 0 50px; } }
  @media (max-width: 500px) {
    .container {
      padding: 0 20px; } }

.star-rating {
  color: #272c35;
  font-size: 18px;
  line-height: 0.75; }
  .star-rating__body {
    max-width: fit-content;
    position: relative; }
    .star-rating__body::before {
      content: "☆☆☆☆☆";
      display: block;
      color: #d9d9d9; }
  .star-rating__active {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden; }
    .star-rating__active::before {
      content: "★★★★★";
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0; }
  .star-rating__items {
    display: flex;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0; }
  .star-rating__item {
    flex: 0 0 20%;
    height: 100%;
    margin: 0;
    opacity: 0; }

.star-rating-clickable {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end; }
  .star-rating-clickable input {
    display: none; }
  .star-rating-clickable label {
    display: block;
    cursor: pointer;
    position: relative; }
    .star-rating-clickable label::before, .star-rating-clickable label::after {
      display: block;
      color: #272c35;
      font-size: 32px;
      line-height: 0.75; }
    .star-rating-clickable label::before {
      content: "☆"; }
    .star-rating-clickable label::after {
      content: "★";
      position: absolute;
      top: 0;
      opacity: 0;
      transition: 0.5s; }
  .star-rating-clickable input:checked ~ label::after {
    opacity: 1; }

.sizes-grid {
  max-width: fit-content; }
  .sizes-grid img {
    width: 100%; }

.checkbox-modal {
  max-width: 524px;
  background-color: #fff;
  padding: 50px;
  text-align: center; }
  .checkbox-modal button {
    width: 20px !important; }
  .checkbox-modal__title {
    color: #f0493e;
    font-size: 30px; }
  .checkbox-modal__description {
    width: 100%;
    margin: 16px auto 0;
    color: #5b626f;
    font-size: 22px;
    line-height: 26px; }
  @media (max-width: 450px) {
    .checkbox-modal {
      padding: 30px 30px; }
      .checkbox-modal__title {
        font-size: 22px;
        line-height: 24px; }
      .checkbox-modal__description {
        margin: 10px auto 0;
        font-size: 20px;
        line-height: 24px; } }

.title-2 {
  text-align: center;
  padding-top: 40px;
  font-style: normal;
  font-weight: 900;
  font-size: 42px;
  line-height: 53px;
  text-transform: uppercase;
  color: #272c35;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain; }
  @media (max-width: 500px) {
    .title-2 {
      font-size: 30px; } }

.innovation__container {
  padding: 0 98px; }
  @media (max-width: 450px) {
    .innovation__container {
      padding: 0 16px; } }

#product .product__title {
  color: #272c35;
  font-size: 42px;
  line-height: 63px;
  font-weight: 700;
  margin-bottom: 48px; }

#product .product_card__img {
  max-width: 295px;
  width: 100%; }
  #product .product_card__img img {
    width: 100%; }

.advances__item img {
  width: 100%; }
